"use client"; // این صفحه دیگر نیازی به use client ندارد، اما برای سادگی نگه می‌داریم

import React, { ReactNode } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";

// ۱. وارد کردن کامپوننت‌های لازم
import { YadakchiAdminLayout, ShopDetailsCard } from "@my-ds/design-system";

// ۲. داده‌های نمونه و ثابت (برای نمایش استاتیک)
const sampleShop = {
  id: "01989e69-5c06-783b-81b8-c47fb2a91137",
  logo: null,
  shopTitle: "مهدی شاپ",
  registrationStatus: "Confirmed",
  name: "مهدی",
  lastname: "اکبری",
  description: "این یک فروشگاه نمونه برای نمایش اطلاعات است.",
  website: "https://example.com",
  tell: "021-12345678",
  // ... سایر فیلدهای مورد نیاز
};

// ۳. تابع ترجمه ساده شده
const translateStatus = (status: string) => {
  if (status === 'Confirmed') return 'تایید شده';
  return status;
}

export default function ShopDetailsPage() {
  const pathname = usePathname();
  const renderLink = (href: string, children: ReactNode) => (
    <Link href={href}>{children}</Link>
  );

  return (
    <YadakchiAdminLayout currentPath={pathname} renderLink={renderLink}>
      <h1 className="text-2xl font-semibold mb-4">جزئیات فروشگاه</h1>
      
      {/* ۴. استفاده از کامپوننت جدید با داده‌های ثابت */}
      <ShopDetailsCard
        shopLogo={sampleShop.logo}
        shopTitle={sampleShop.shopTitle}
        shopStatus={translateStatus(sampleShop.registrationStatus)}
        shopStatusVariant={
          sampleShop.registrationStatus === "Confirmed" ? "success" : "warning"
        }
        items={[
          { title: "نام فروشنده", value: `${sampleShop.name} ${sampleShop.lastname}` },
          { title: "توضیحات", value: sampleShop.description },
          { title: "وب‌سایت", value: sampleShop.website },
          { title: "تلفن", value: sampleShop.tell },
        ]}
      />
    </YadakchiAdminLayout>
  );
}